import React from 'react';
//weui
import { Tab, TabBarItem, Article } from 'react-weui';
import { Footer,FooterText } from 'react-weui';
//svg
import { Svgs } from "../common/Svgs";
//
import "./mobile.less";
//
import { XDY } from "./xdy/XDY";

export class Mobile extends React.Component{
    render(){
        return (
            <Tab type="tabbar">
                <TabBarItem icon={<Svgs type="clan-brother-icon" style={{width: '25px'}}/>} label="">
                    <Article style={{padding: 0}}>
                        <XDY/>
                    </Article>
                </TabBarItem>
                <TabBarItem icon={<Svgs type="clan-old-icon" style={{width: '25px'}}/>} label="">
                    <Article>
                        
                    </Article>
                </TabBarItem>
                <TabBarItem icon={<Svgs type="contact" style={{width: '25px'}}/>} label="">
                    <Article>
                        <h1></h1>
                        <section>
                            <h2 className="title">QQ</h2>
                            <p> 594712902</p>
                        </section>
                        <Footer>
                            <FooterText>Copyright &copy; 2008-2018 </FooterText>
                        </Footer>
                    </Article>
                </TabBarItem>
            </Tab>
        );
    }
}


